
import Badge from 'vux/src/components/badge/index.vue'


export default {
components: {Badge},

data() {
return {
flag: false,
message: "首页",
comingNotice: false,
shake: 'shake',
noshake: 'noshake'
};
},

computed: {},

beforeMount() {},

mounted() {},

methods: {
gosetting() {
this.$router.push("/ids/setting");
},
goNotice() {
this.$router.push("/msg/notice-list");
}
},

created() {
if (this.$route.path == "/dui/me") {
this.message = "我的";
} else if (this.$route.path == "/dui/work") {
this.message = "服务";
} else {
this.message = "首页";
}
},

watch: {
message: function(val, oldVal) {

switch (val) {
case "首页":
this.$router.push("/dui/home");
break;
case "服务":
this.$router.push("/dui/work");
break;
case "我的":
this.$router.push("/dui/me");
break;
}
},
'$route.path':function (newVal, oldVal) {
if (newVal == "/dui/me") {
this.message = "我的";
} else if (newVal == "/dui/work") {
this.message = "服务";
} else {
this.message = "首页";
}
},
}
};



